import React,{useContext,useState,useEffect} from 'react';
import { MyContext } from '../contexts/MyContext';
import {Link} from 'react-router-dom';
import { FiArrowUp } from "react-icons/fi";
function Footer() {

    useEffect( () => {
        window.addEventListener('scroll', toggleVisibility);
      
      }, []);
      
      const scrollToTop =()=> {
        window.scrollTo({
          top: 0,
          behavior: "smooth"
        });
      }
      
    const [is_visible, set_visible] = useState(false);
    
    const toggleVisibility =()=> {
      if (window.pageYOffset > 700) {
        set_visible(true);
      
      } else {
        set_visible(false);
      }
    }
   
  

  return (
      <>
    <div id="Footer" className="footer">
        <div className="container">
            <div className="row">
    
              <div className="copyrighitsss">
                  <div className="col-md-6 ">
                      <p><Link to="/" > website </Link> Â© All Rights Reserved - 2021 </p>
                  </div>
                
                </div> 
          </div>
        </div>
      </div>
         
          <div className="clearfix"></div>
         
          <div className="footersss2">
         
          {is_visible && ( <>
              <span onClick={scrollToTop} className="scrolls"> <FiArrowUp /> </span><span className="ltx-hex"><span className="ltx-hex-inner"></span></span>	
              </>
              )}
          </div>
            
       
        </>
  );
}

export default Footer;
